import axios from 'axios';

const url = '/api/majors';

class MajorService {
    static getMajorData() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(major => ({
                        ...major,
                        createdAt: new Date(major.createdAt)
                    }))
                );
            } catch (error) {
                reject(error);
            }
        })
    }
}

export default MajorService;