<script>
import MajorService from "../MajorService";

export default {
  name: "MajorComponent",
  data() {
    return {
      majors: [],
      error: "",
      text: "",
      selected: "",
      seen: true
    };
  },
  async created() {
    try {
      this.majors = await MajorService.getMajorData();
    } catch (err) {
      this.error = err.message;
    }
  },
  computed: {
    filteredMajors: function() {
      return this.majors.filter(major => {
        return major.MajorName.match(this.selected);
      });
    }
  }
};
</script>

